// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, APP_NAME } from '../../config';
import Network from './network';
import DataCardPlan from './data_card_plan';
// sections
// hooks
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------
export default function DocsDataCard() {
  const { themeStretch } = useSettings();

  const {user} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Data Card API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
              This section contains the recommended flow for integrating  Data Card services on the {APP_NAME} RESTful API.
                           </Typography>

        
                <Markdown children={`<hr> <h3>Available Endpoints</h3><br/> <p>To integrate the ${APP_NAME} Data Card Subscription Payment RESTful API, the endpoints below applies:</p>
                <p><br></p><p>
                <ol> <li>network</li> <li>plan_type</li> <li>quantity</li> <li>card_name</li> </ol>
                
                `} />
           <Network />
            <DataCardPlan />
           <Markdown children={`<hr> <h3>PURCHASE PRODUCT</h3><br/> <p>Using a POST method,  Data card bundle can with the endpoint below:</p>
                <p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/data_card</blockquote> <hr> <h3>Code Sample</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                $paypload = array(
                    'network' => 1,
                     'plan_type' => 1,
                     'quantity' => 2,
                     'card_name' => '${user?.username}',
                       );
                     $ch = curl_init();
                     curl_setopt($ch, CURLOPT_URL, '${HOST_API}/api/data_card');
                     curl_setopt($ch, CURLOPT_POST, 1);
                     curl_setopt($ch, CURLOPT_POSTFIELDS, json_encode($paypload));
                     curl_setopt($ch, CURLOPT_RETURNTRANSFER, true);
                     $headers = [
                          "Authorization: Token ${user?.apikey}",
                         'Content-Type: application/json'
                     ];
                     curl_setopt($ch, CURLOPT_HTTPHEADER, $headers);
                     $response = curl_exec($ch);
                     curl_close($ch);
                </pre><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "network": "MTN",
                    "request-id": "Data_card_6346d0932500a",
                    "amount": 200,
                    "quantity": "2",
                    "status": "success",
                    "message": "MTN Data Card Printing Successful",
                    "card_name": "${user?.username}",
                    "oldbal": "42686",
                    "newbal": 42486,
                    "system": "API",
                    "serial": "856073001_3,348311767_4",
                    "pin": "data_card_400mb,data_card_500mb",
                    "load_pin": "*1234#",
                    "check_balance": "*1234#"
                }
                </pre>
                
                `} />
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
